<template>
    <div class="page-user">
        <x-wrapper
                title=""
                service="user"
                :columns="columns"
                :model="model"
                :rules="rules"
                :filter="filter"
                ref="wrapper"
                :orders="['-changed']"
                :height="windowHeight - 260"
        >
            <div slot="dialog" slot-scope="{ model }">
                <FormItem :label="userModel.name" prop="name">
                    <Input v-model="model.name" :placeholder="''+ userModel.name"  />
                </FormItem>
                <FormItem :label="userModel.username" >
                    <Input v-model="model.username" :placeholder="''+ userModel.username"  />
                </FormItem>

                <FormItem :label="userModel.password" >
                    <Input v-model="model.password" type="password" :placeholder="''+ userModel.password"  />
                </FormItem>

                <FormItem :label="userModel.mobile" >
                    <Input v-model="model.mobile" :placeholder="''+ userModel.mobile"  />
                </FormItem>

                <FormItem :label="userModel.sex">
                    <RadioGroup v-model.number="model.sex">
                        <Radio :label="1"></Radio>
                        <Radio :label="0"></Radio>
                    </RadioGroup>
                </FormItem>
                <FormItem :label="userModel.status">
                    <RadioGroup v-model.number="model.status">
                        <Radio :label="1"></Radio>
                        <Radio :label="0"></Radio>
                    </RadioGroup>
                </FormItem>

                <FormItem :label="userModel.admin">
                    <i-switch v-model="model.admin" :true-value="1" :false-value="0"></i-switch>
                </FormItem>
            </div>
        </x-wrapper>

        <Modal title="" v-model="dialog">
            <Input type="password" v-model="newPassword" placeholder="" />
            <div slot="footer">
                <Button @click="dialog = false"></Button>
                <Button @click="onOk" type="primary"></Button>
            </div>
        </Modal>
    </div>
</template>
<script>
import types from "@/types";
const { userModel, newUser } = types;
import Vue from "vue";
import uploadPic from "@/components/uploadPic";
import { mapState } from "vuex";
import roleUserService from "@/services/role_user";
import { resetPasword } from "@/services/user";

export default {
  name: "User",
  components: {
    uploadPic
  },
  computed: {
    ...mapState("common", ["roles"]),
    ...mapState(["windowHeight"])
  },
  data() {
    return {
      userModel,
      columns: [
        {
          width: 40,
          align: "center",
          type: "selection"
        },
        {
          title: userModel.name,
          width: 150,
          key: "name",
        },
        {
          title: userModel.username,
          width: 150,
          key: "username",
        },
        {
          title: userModel.status,
          width: 100,
          key: "status",
          sortable: "custom",
          render: (h, { row }) => {
            return h(
              "Tag",
              {
                props: {
                  color: row.status == 1 ? "success" : "default",
                  size: "small",
                  type: "border"
                },
                style: {
                  fontSize: "12px"
                }
              },
              row.status == 1 ? "" : ""
            );
          }
        },
        {
          title: userModel.admin,
          width: 120,
          key: "admin",
          sortable: "custom",
          render: (h, { row }) => {
            return h("div", {}, row.admin == 1 ? "" : "");
          }
        },
        {
          title: userModel.sex,
          sortable: "custom",
          width: 100,
          key: "sex",
          render: (h, { row }) => {
            return h("div", {}, Vue.filter("sex")(row.sex));
          }
        },
        {
          title: userModel.mobile,
          width: 120,
          key: "mobile"
        },
        {
          title: "",
          minWidth: 150,
          key: "created",
          sortable: "custom",
        },
        {
          title: "",
          fixed: "right",
          width: 120,
          align: "center",
          render: (h, { row }) => {
            return h("div", [
              h(
                "iconButton",
                {
                  props: {
                    icon: "iconfont icon-bianji",
                    type: "primary",
                    tooltip: null,
                    placement: "left"
                  },
                  style: {
                    marginRight: "8px"
                  },
                  on: {
                    click: () => this.$refs.wrapper.onEdit(row)
                  }
                },
                ""
              ),
              // h(
              //   "iconButton",
              //   {
              //     props: {
              //       icon: "iconfont icon-iconfontmima",
              //       type: "primary",
              //       tooltip: "",
              //       placement: "right"
              //     },
              //     style: {
              //       marginRight: "8px"
              //     },
              //     on: {
              //       click: () => this.onRestPwd(row)
              //     }
              //   },
              //   ""
              // ),
              h(
                "iconButton",
                {
                  props: {
                    type: "error",
                    icon: "iconfont icon-shanchu",
                    tooltip: null,
                    placement: "right"
                  },
                  on: {
                    click: () =>
                      this.$refs.wrapper.onDelete(
                        row.id,
                        `${row.name}`
                      )
                  }
                },
                ""
              )
            ]);
          }
        }
      ],
      rules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        sn: [{ required: true, message: "", trigger: "blur" }],
        account: [{ required: true, message: "", trigger: "blur" }],
        password: [{ required: true, message: "", trigger: "blur" }]
      },
      filter: [
        {
          type: "input",
          name: userModel.name,
          field: "name",
          placeholder: `${userModel.name}`
        },
        {
          type: "input",
          name: userModel.account,
          field: "account",
          placeholder: `${userModel.account}`
        },
        {
          type: "select",
          name: userModel.sex,
          field: "sex",
          placeholder: "",
          data: [
            { value: 0, name: "" },
            { value: 1, name: "" },
            { value: 2, name: "" }
          ]
        },
        {
          type: "input",
          name: userModel.idcard,
          field: "idcard",
          placeholder: `${userModel.idcard}`
        },
        {
          type: "input",
          name: userModel.email,
          field: "email",
          placeholder: `${userModel.email}`
        },
        {
          type: "input",
          name: userModel.job,
          field: "job",
          placeholder: `${userModel.job}`
        },
        {
          type: "input",
          name: userModel.mobile,
          field: "mobile",
          placeholder: `${userModel.mobile}`
        },
        {
          type: "input",
          name: userModel.smobile,
          field: "smobile",
          placeholder: `${userModel.smobile}`
        },
        {
          type: "input",
          name: userModel.telephone,
          field: "telephone",
          placeholder: `${userModel.telephone}`
        }
      ],
      total: 0,
      model: newUser,
      active: "one",
      targetRole: [],
      dialog: false,
      newPassword: "",
      selectUser: null
    };
  },
  methods: {
    onOk() {
      if (this.newPassword == "") {
        return this.$Message.warning("");
      }
      resetPasword({
        id: [this.selectUser.id],
        password: this.newPassword
      }).then(() => {
        this.dialog = false;
        this.$Message.success("");
      });
    },
    onRestPwd(row) {
      this.selectUser = row;
      this.dialog = true;
      this.newPassword = "";
    },
  }
};
</script>
<style lang="less">
.page-user {
  height: 100%;
}
.role-list {
  border: 1px solid #ddd;
  padding: 5px 10px 10px 10px;
  &__item {
    margin-top: 5px;
  }
}
</style>
